import * as _ from '../utils';
import { BaseTheadConfig } from './BaseTheadConfig';
export class TheadContainerConfig extends BaseTheadConfig {
    public span1: number = 1;
    public span2: number = 1;
    constructor() {
        super();
        const style = {
            overflow: 'hidden',
            boxSizing: 'border-box',
            whiteSpace: 'nowrap',
            textOverflow: 'ellipsis',
            background: '#fff',
        };
        _.objectSet(this.style, style, 'union');
    }
}
