import * as _ from '../utils';
import { BaseTheadConfig } from './BaseTheadConfig';
export class IndexTheadConfig extends BaseTheadConfig {
    constructor() {
        super();
        const style = {
            overflow: 'hidden',
            boxSizing: 'border-box',
            whiteSpace: 'nowrap',
            textOverflow: 'ellipsis',
            userSelect: 'none',
            background: '#fff',
            zIndex: 99
        };
        _.objectSet(this.style, style, 'union');
    }
}
